  public indiciosXNUCBackup: Indicio[] = [];
  public indicioXNUCDataSelected: Indicio;
  public indiciosXNUCToPDF: Indicio[] = [];
  public indicioDataSelectedToPDF: Indicio;
  
  public servidorpublicoXNUC: ServidorPublico[] = [];

  public servidorpublicoXIndicio: ServidorPublico[] = [];

